@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Regular.ttf");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Roboto-Light';
  src: url("../fonts/Roboto-Light.ttf");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Roboto-Bold';
  src: url("../fonts/Roboto-Bold.ttf");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

*,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

html {
  font-size: 16px; }

body {
  font-family: 'Roboto', sans-serif;
  background: #f4fffb; }

a {
  text-decoration: none; }

.btn {
  display: inline-block;
  text-align: center;
  text-decoration: none;
  color: #929292;
  border: solid 1px transparent;
  outline: none;
  cursor: pointer; }
  .btn:active {
    transform: translateY(2px);
    filter: saturate(150%); }

.container {
  display: flex;
  flex-direction: row;
  max-width: 100%; }

.wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column; }

/* Стили для подложки */
.overlay {
  /* Скрываем подложку  */
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  transition: 0.3s all; }

.footer,
.header {
  display: flex;
  justify-content: flex-end;
  margin: 2.4rem; }
  .footer__info,
  .header__info {
    display: flex;
    flex-wrap: nowrap;
    vertical-align: middle;
    font-size: 0.8rem;
    color: #929292; }

.header__text {
  height: 1.8rem;
  margin: auto 0.7rem;
  padding-top: 0.1rem;
  line-height: 1.8rem; }

.footer__link {
  height: 1.8rem;
  margin: auto 0.7rem;
  line-height: 1.8rem; }
  .footer__link-logo {
    vertical-align: middle;
    padding-right: 0.2rem; }

.fix-block {
  display: flex;
  flex-direction: column;
  padding-right: 3rem;
  max-width: 600px;
  min-height: 100%;
  color: #929292;
  background: rgba(255, 245, 245, 0.95); }
  .fix-block__header-h1 {
    margin-top: 13rem;
    margin-bottom: 1.8rem;
    margin-left: 4.8rem;
    text-transform: uppercase; }
  .fix-block__header-h2 {
    font-size: 1.5rem;
    text-align: right; }

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 3rem; }
  .card-content {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .card__main {
    max-width: 40rem;
    margin-bottom: 4rem;
    margin-left: 2rem;
    margin-right: 2rem;
    background-color: rgba(255, 245, 245, 0.95);
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.2); }
  .card__header {
    margin-top: 1.4rem;
    font-weight: 500;
    font-size: 1.7rem;
    text-align: center;
    color: #89B1CE; }
    .card__header-main {
      margin-top: 1.6rem;
      font-size: 2.3rem; }
  .card__text {
    margin-bottom: 3rem;
    padding: 1.1rem;
    font-size: 1.1rem;
    line-height: 1.6rem;
    color: rgba(0, 0, 0, 0.541327); }
    .card__text-main {
      font-size: 1.3rem;
      line-height: 2rem; }
  .card-section {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    padding: 2rem; }
    .card-section__secondary {
      max-width: 24.5rem;
      margin-left: 1rem;
      margin-right: 1rem;
      box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.2);
      background-color: #F3FCFF; }
  .card__btn {
    position: absolute;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 11rem;
    height: 2.6rem;
    color: #765A5A;
    background: rgba(238, 198, 198, 0.1); }

.modal {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  max-width: 33rem;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -60%);
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  background: #fff;
  border-radius: 0.5rem;
  z-index: 10;
  transition: all 0.3s; }
  .modal-content {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    z-index: 100; }
  .modal__header {
    margin: 1.6rem auto;
    font-weight: 500;
    font-size: 2rem;
    line-height: 2.2rem;
    display: flex;
    justify-content: center;
    color: #89B1CE; }
  .modal-close {
    padding: 20px;
    font-size: 1rem;
    color: rgba(0, 0, 0, 0.541327);
    align-self: flex-end;
    margin-right: 0.5rem;
    cursor: pointer; }
  .modal .btn {
    display: block; }

.modal-text {
  width: 100%;
  max-width: 28rem;
  margin-bottom: 2rem;
  font-size: 1.2rem;
  line-height: 1.4rem;
  color: rgba(0, 0, 0, 0.541327);
  display: flex;
  flex-direction: column;
  align-items: left; }
  .modal-text__desc {
    margin-bottom: 1.4rem; }

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible; }

@media screen and (min-width: 1520px) {
  html {
    font-size: 20px; } }

@media screen and (max-width: 1200px) {
  .container {
    flex-direction: column; }
  .fix-block {
    width: 100%;
    max-width: inherit; }
    .fix-block__header-h1 {
      margin-top: 2rem; }
    .fix-block__header-h2 {
      margin-bottom: 2rem;
      text-align: left;
      margin-left: 3rem; } }

@media screen and (max-width: 920px) {
  .card {
    margin-bottom: 5rem; }
    .card__main {
      margin: 0 auto; }
    .card-section__secondary {
      max-width: 40rem; } }

@media screen and (max-width: 768px) {
  html {
    font-size: 14px; } }

@media screen and (max-width: 425px) {
  html {
    font-size: 10px; }
  .card {
    margin-bottom: 3rem; }
    .card-section__secondary {
      margin: 0 auto 3rem; } }
